
import "../../styles/contacts.css";

import React from "react";

class ContactInfo extends React.Component
{
    id      = "";
    label   = "";
    image   = "";
    display = "";
    link    = "";

    self   = null;
    labels = null;

    onEnter = () =>
    {
        if (this.labels)
        {
            for (let i = 0; i < this.labels.length; ++i)
            {
                this.labels[i].style.maxWidth     = this.labels[i].scrollWidth + "px";
                this.labels[i].style.paddingRight = "10px";
            }
        }
    }

    onExit = () =>
    {
        if (this.labels)
        {
            for (let i = 0; i < this.labels.length; ++i)
            {
                this.labels[i].style.maxWidth     = null;
                this.labels[i].style.paddingRight = 0;
            }
        }
    }

    getInfo = () =>
    {
        this.id = "ContactInfo" + this.label;

        return (
            <a id={this.id} className="Contact" href={this.link} target="_blank" rel="noopener noreferrer" onMouseOver={this.onEnter} onMouseLeave={this.onExit}>
                <img src={this.image} alt={this.label}></img>
                <span className="Label">{this.display}</span>
            </a>
        );
    }

    componentDidMount()
    {
        this.self = document.getElementById(this.id);

        if (this.self)
        {
            this.labels = this.self.getElementsByClassName("Label");
        }
    }
}

export default ContactInfo;
