import '../../styles/project_brief.css';
import '../../styles/aspect_ratios.css';

import React        from 'react';
import Tags         from '../templates/project_tags.js';
import ProjectBrief from '../templates/project_brief.js';
import DigipenLogo  from '../../media/digipen.svg';
import SteamLogo    from '../../media/steam.svg';

class EosBrief extends ProjectBrief
{
    render()
    {
        this.name  = 'Eos (2019)';
        this.tags  = [Tags.programming, Tags.leadership];
        this.links =
        [
              { "name":"Steam"                , "link":"https://store.steampowered.com/app/1571180/Eos/", "image":SteamLogo   }
            , { "name":"Digipen Games Gallery", "link":"https://games.digipen.edu/games/eos"            , "image":DigipenLogo }
        ];

        return (
            <div className="Project-Brief">
                <div className="Project-Block">
                    <div className="AspectRatio _16-9">
                        <iframe
                            title       = "Eos Trailer"
                            width       = "100%"
                            height      = "100%"
                            src         = "https://www.youtube.com/embed/wH1Y8KvImdE"
                            frameborder = "0"
                            crossorigin = "anonymous"
                            allow       = "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture;"
                            allowfullscreen
                        >
                        </iframe>
                    </div>
                </div>

                <div id="jump" className="Project-Block">
                    <this.get_title />
                    <this.get_tags />

                    <div className="Project-Description">
                        Eos is a 3D puzzle game made by an 11 person team, Bonsai Tavern, for our 3rd year Digipen
                        games project. I was the producer for the first semester before stepping down to fully
                        focus on my technical responsibilities in the second semester. Some of the things I've
                        implemented include:

                        <ul>
                            <li>Property Reflection  </li>
                            <li>De/Serialiser        </li>
                            <li>Configuration Manager</li>
                            <li>3D Animations        </li>
                            <li>Prefab System        </li>
                        </ul>
                    </div>

                    <this.get_links />
                </div>
            </div>
        );
    }
}

export default EosBrief;
