import '../../styles/project_brief.css';

import React from 'react';

class ProjectBrief extends React.Component
{
    name;
    links;
    tags;

    get_title = () =>
    {
        return (<div className="Project-Title">{this.name}</div>);
    }

    get_links = () =>
    {
        const link_list = this.links.map
        ((element) =>
            <a href={element.link} target="_blank" rel="noopener noreferrer">
                <img height="100%" alt={element.name} src={element.image}></img>
            </a>
        );
        return (<div className="Project-Link"><b>Download:</b><span>{link_list}</span></div>);
    }

    get_tags = () =>
    {
        const tags_list = this.tags.map((element) => <span className={element}>#{element}</span>);
        return (<div className="Project-Hashtag">{tags_list}</div>);
    }
}

export default ProjectBrief;
