import "../../styles/contacts.css";

import React        from "react";
import ContactInfo  from "./contact_info.js";
import EmailIcon    from "../../media/email.svg";
import GitHubIcon   from "../../media/github.svg";
import LinkedInIcon from "../../media/linkedin.svg";

class EmailContact extends ContactInfo
{
    render()
    {
        this.label   = "Email";
        this.image   = EmailIcon;
        this.display = this.label;
        this.link    = "mailto:gabrielle@gtsc.dev";

        return this.getInfo();
    }
}

class GitHubContact extends ContactInfo
{
    render()
    {
        this.label   = "GitHub";
        this.image   = GitHubIcon;
        this.display = this.label;
        this.link    = "https://github.com/XxGTSCxX";

        return this.getInfo();
    }
}

class LinkedInContact extends ContactInfo
{
    render()
    {
        this.label   = "LinkedIn";
        this.image   = LinkedInIcon;
        this.display = this.label;
        this.link    = "https://www.linkedin.com/in/gabrielle-tan-suan-choo/";

        return this.getInfo();
    }
}

class ContactList extends React.Component
{
    render()
    {
        return (
            <div className="Contacts">
                <EmailContact/>
                <GitHubContact/>
                <LinkedInContact/>
            </div>
        );
    }
}

export default ContactList;
