import "../styles/navbar.css";

import   React                     from "react";
import { navbar_position, NavTab } from "./templates/navtab.js";
import   TimelineIcon              from "../media/timeline.svg";
import   AboutMeIcon               from "../media/aboutme.svg";
import   DevLogsIcon               from "../media/devlogs.svg";

class TimelineTab extends NavTab
{
    render()
    {
        this.name = "Timeline";
        this.icon = TimelineIcon;
        this.link = "/Timeline";
        this.pos  = navbar_position.middle;

        return this.getTab();
    }
}

class AboutMeTab extends NavTab
{
    render()
    {
        this.name = "Profile";
        this.icon = AboutMeIcon;
        this.link = "/Profile";
        this.pos  = navbar_position.first;

        return this.getTab();
    }
}

class DevLogsTab extends NavTab
{
    render()
    {
        this.name = "Dev Logs";
        this.icon = DevLogsIcon;
        this.link = "/DevLogs";
        this.pos  = navbar_position.last;

        return this.getTab();
    }
}

class NavBar extends React.Component
{
    render()
    {
        return (
            <navbar className="NavBar">
                <AboutMeTab/>
                <TimelineTab/>
                <DevLogsTab/>
            </navbar>
        );
    }
}

export default NavBar;
