import "../../styles/navtab.css";
import "../../styles/animations.css";

import   React   from "react";
import { Link  } from "react-router-dom";

const navbar_position = 
{
      first : -1
    , middle:  0
    , last  :  1
}

class NavTab extends React.Component
{
    name = "";
    icon = "";
    link = "";
    pos  = navbar_position.middle;

    id      = "";
    name_id = "";

    self      = null;
    name_elem = null;

    onHover = () =>
    {
        this.name_elem.style.maxWidth           = this.name_elem.scrollWidth + "px";
        this.name_elem.style.paddingRight       = "15px";
        this.self.style.borderTopRightRadius    = "15px";
        this.self.style.borderBottomRightRadius = "15px";
    };

    onHoverExit = () =>
    {
        this.name_elem.style.maxWidth     = null;
        this.name_elem.style.paddingRight = 0;
        this.self.style.borderTop         = null;
        this.self.style.borderBottom      = null;

        switch (this.pos)
        {
        case navbar_position.first:
            this.self.style.borderTopRightRadius    = "15px";
            this.self.style.borderBottomRightRadius =  "0px";
            break;
        case navbar_position.middle:
            this.self.style.borderTopRightRadius    = "0px";
            this.self.style.borderBottomRightRadius = "0px";
            break;
        case navbar_position.last:
            this.self.style.borderTopRightRadius    =  "0px";
            this.self.style.borderBottomRightRadius = "15px";
            break;
        }
    };

    getTab = () =>
    {
        this.name    = this.name.replace(" ", "_");
        this.id      = "NavTab" + this.name;
        this.name_id = this.id  + "Name";

        return (
            <Link id={this.id} className="NavTab" to={this.link} onMouseEnter={this.onHover} onMouseLeave={this.onHoverExit}>
                <img src={this.icon} alt={this.name}></img>
                <div id={this.name_id} className="Name">{'>'} cd {this.name}<span className="Blinking">_</span></div>
            </Link>
        );
    };

    componentDidMount()
    {
        this.self      = document.getElementById(this.id     );
        this.name_elem = document.getElementById(this.name_id);

        this.onHoverExit();
    }
}

export { navbar_position, NavTab };
