import "../../styles/header.css";
import "../../styles/animations.css";
import logo from "../../media/favicon.svg";
import React from "react";

class Header extends React.Component
{
    id      = "Header";
    padding = "Header-Padding";

    name = "Gabrielle Tan";
    role = "Software Developer";

    max_height = 0;
    min_height = 0;

    componentDidMount()
    {
        const onresize = () =>
        {
            this.max_height = window.innerHeight;
            this.min_height = Math.max(0.0 * window.innerHeight, 0);

            onscroll();
        }

        const onscroll = () =>
        {
            const self       = document.getElementById(this.id     );
            const padding    = document.getElementById(this.padding);
            const scroll     = document.documentElement.scrollTop;
            const new_height = Math.max(this.min_height, Math.min(this.max_height, this.max_height - scroll));

            if (scroll < this.max_height)
            {
                self.style.height    = new_height + "px";
                padding.style.height = scroll     + "px";
            }
            else
            {
                self.style.height    = this.min_height + "px";
                padding.style.height = this.max_height + "px";
            }
        }

        window.addEventListener("resize", onresize);
        window.addEventListener("scroll", onscroll);

        onresize();
    }

    render()
    {
        return (
            <div className="Header-Container">
                <div id={this.padding}></div>
                <header id={this.id} className="Header">
                    <img src={logo} className="Header-Logo" alt="logo"></img>
                    <div className="Header-Text">
                        <div className="Header-Title"   >{this.name}</div>
                        <div className="Header-Subtitle">{this.role}<span className="Blinking">_</span></div>
                    </div>
                </header>
            </div>
        );
    }
}

export default Header;
